  cookie_bar_text_dnt: 'To offer you a better browsing experience, we use cookies. We respect your "Do not track" browser setting, analyzing is deactivated.',
  data_privacy_content: '<strong>CovApp â Data Protection Notice (Status: 20.3.2020)</strong>\n\n<strong>1. Data Controller</strong>\n\nThe CovApp is a joint project of CharitÃ© - UniversitÃ¤tsmedizin Berlin (responsible for the provision and hosting of the CovApp) and the nonprofit D4L data4life gGmbH.\n\nThe organisation responsible for the processing of personal data is\n\nCharitÃ© - University Medicine Berlin\nCharitÃ©platz 1\n10117 Berlin\nGermany\nPhone: <a href="tel:+493045050">+49 30 45050</a>\nWebsite: <a href="https://www.charite.de/" target="_blank">www.charite.de</a>\n\nIf you have any questions regarding the processing of your personal data, as well as your rights regarding data protection, please contact\n\nPhone: <a href="tel:+4930450580016">+49 30 450580-016</a>\nEmail: <a href="mailto:datenschutz@charite.de">datenschutz(at)charite.de</a>\n\n<strong>2. Which data is collected and how is it processed?</strong>\n\nThe CovApp is used to ask questions about current symptoms, your last trips and possible contacts. These data are not attributed by name.\n\nThe CovApp is a web application. The following data is automatically transferred to the CharitÃ©\'s web server, which is operated by D4L data4life gGmbH as a processor on behalf of the CharitÃ©, with a computer center in Frankfurt at the subcontractor Amazon Web Services (AWS):\n\n- IP address of the device used for the retrieval\n- Web address (URL) of the page from which the file was requested (referrer)\n- Date and time of the request\n- Data volume transferred\n- Description of the type of web browser used\n\nThe above-mentioned data is not stored after the app has been used, so that no conclusion can be drawn as to the identity of the user.\n\nThe answers to the questions answered in the app and the recommendations for action are stored exclusively locally on the terminal device you use (smartphone, tablet, or PC). This data will â if you wish â be summarized in a QR code. You can show this QR code when being tested for COVID-19, at a doctor\'s or hospital to facilitate the collection of the necessary anamnesis data.\n\nYou can delete the data stored by CovApp on your terminal device at any time by clicking "Delete answers" at the end of the questionnaire. \n\n<strong>3. For what purposes and on what legal basis are the data processed?</strong>\n\nThe CovApp is intended to make it easier for users to assess for themselves whether there is a need for further clarification regarding a COVID-19 infection and offers concrete recommendations for action. The app thus serves to combat the current COVID-19 pandemic. The legal basis of the processing is Article 6.1. lit. d) and e), Art. 9. 2 c), h), i) of the General Data Protection Regulation in conjunction with Â§ 14.1.3 of the Berlin Data Protection Act.\n\nIn all other respects, the CharitÃ©\'s data protection declaration applies, available at <a href="https://www.charite.de/service/datenschutz/" target="_blank">https://www.charite.de/service/datenschutz/</a>',
  data_privacy_headline: "Privacy policy",
  disclaimer_headline: "Disclaimer",
  disclaimer_paragraph_1: "Dear users,\nThe coronavirus pandemic is changing, and in the future, we will only test people in Germany who meet the following criteria:\n<ul><li>People who have had contact with infected individuals and have symptoms</li><li>People belonging to risk groups and those having pre-existing conditions that may intensify the severity of COVID-19 disease</li><li>People who could further spread the coronavirus</li></ul>\nIf you have used the app in the past, you will notice that the assessment is now different. We will continue to update our protocols in accordance with relevant authorities and institutional recommendations. For this, we are working closely with the Robert Koch Institute and the Federal Ministry of Health.\n\nThe main message is: Please stay at home and do your part to help slow down the spread.\n\n<strong>Legal disclaimer</strong>\nUsage of this app <strong>does not substitute medical treatment</strong> by a physician or clinical diagnostics. The CovApp serves to improve the procedures at the CharitÃ© examination centre and other outpatient clinics in connection with the novel coronavirus SARS-CoV-2. If you currently feel seriously ill, please seek medical help.",
