  imprint_content: '<strong>Provider </strong>\nCharitÃ© â UniversitÃ¤tsmedizin Berlin\nPostal address: CharitÃ©platz 1, 10117 Berlin \nDie CharitÃ© â UniversitÃ¤tsmedizin Berlin is a public-law institution. It is legally represented by the chairman of the board.\n\n<strong>Contact</strong>\nSend an email to <a href="mailto:covapp@charite.de">covapp@charite.de</a> for any praise, notes, or criticism\nInternet: <a href="https://www.charite.de" target="_blank">https://www.charite.de</a>\n\n<strong>Responsible person in terms of media law</strong>\nProf. Dr. Heyo K. Kroemer, The Chairman of the Board of CharitÃ© â UniversitÃ¤tsmedizin Berlin \n\nResponsible Content:\nProject Lead: Dr. Valerie Kirchberger, M. Sc.\nHead of Product: Dipl. Kffr. Claudia Hartmann\n\nResponsible Software development:\nHead CharitÃ©: Dr. Alexander H. Thieme M. Sc.\nSAP Integration: Peter Heumann\n\nCovApp Team: Antonia Rollwage, Peter Gocke\n\n<strong>Regulatory authority </strong>\nThe Governing Mayor of Berlin â Senate Chancellery Higher Education and Research \nContact: <a href="https://www.berlin.de/rbmskzl/" target="_blank">https://www.berlin.de/rbmskzl/</a>\n\nSenate Chancellery Health, Care and Equality\n<a href="https://www.berlin.de/sen/gpg/" target="_blank">https://www.berlin.de/sen/gpg/</a>\n\nSales tax number â Identification number: DE 228847810',
