  data_privacy_content: '<strong>Hinweise zum Datenschutz bei der CovApp der CharitÃ© (Stand: 20.3.2020)</strong>\n\n<strong>1. Verantwortlichkeit</strong>\n\nDie CovApp ist ein Gemeinschaftsprojekt der CharitÃ© â UniversitÃ¤tsmedizin Berlin (Verantwortlich fÃ¼r die Bereitstellung und das Hosting der CovApp) und der gemeinnÃ¼tzigen D4L data4life gGmbH.\n\nFÃ¼r die Verarbeitung personenbezogener Daten verantwortlich ist die\n\nCharitÃ© â UniversitÃ¤tsmedizin Berlin\nCharitÃ©platz 1\n10117 Berlin\nDeutschland\nTel: <a href="tel:+493045050">+49 (0) 30 450 50</a>\nWebsite: <a href="https://www.charite.de/" target="_blank">www.charite.de</a>\n\nBei Fragen zur Verarbeitung Ihrer personenbezogenen Daten, sowie zu Ihren Rechten rund um den Datenschutz, wenden Sie sich bitte an:\nTelefon: <a href="tel:+4930450580016">+49 (0) 30 450 580 016</a>\nE-Mail: <a href="mailto:datenschutz@charite.de">datenschutz(at)charite.de</a>\n\n<strong>2. Welche Daten werden erhoben und wie werden sie verarbeitet?</strong>\n\nMit der CovApp werden Fragen nach aktuellen Symptomen, Ihren letzten Reisen und mÃ¶glichen Kontakten gestellt. Eine namentliche Zuordnung dieser Daten erfolgt nicht.\n\nBei der CovApp handelt es sich um Web-Anwendung. Dabei werden die folgenden Daten automatisch an den Web-Server der CharitÃ©, der im Auftrag der CharitÃ© von D4L data4life gGmbH als Auftragsverarbeiter betrieben wird, mit einem Rechenzentrum in Frankfurt bei dem Unterauftragsverarbeiter Amazon Web Services (AWS), Ã¼bertragen:\n\n- IP-Adresse des beim Abruf verwendeten GerÃ¤ts\n- Web-Adresse (URL) der Seite, von der aus die Datei angefordert wurde (Referrer)\n- Datum und Uhrzeit der Anforderung\n- Ãbertragene Datenmenge\n- Beschreibung des Typs des verwendeten Webbrowsers\n\nDie oben genannten Daten werden nach der Verwendung der App nicht gespeichert, sodass kein RÃ¼ckschluss auf die IdentitÃ¤t des Nutzers/der Nutzerin mÃ¶glich ist.\n\nDie Antworten auf die in der App beantworteten Fragen und die Handlungsempfehlungen werden ausschlieÃlich lokal auf dem von Ihnen genutzten EndgerÃ¤t (Smartphone, Tablet oder PC) gespeichert. Diese Daten werden â sofern Sie dies wÃ¼nschen â in einem QR-Code zusammengefasst. Sie kÃ¶nnen diesen QR-Code beim DurchfÃ¼hren eines Tests auf COVID-19 bei einem Arzt oder Krankenhaus vorzeigen, um die Erfassung von erforderlichen Anamnesedaten zu erleichtern.\n\nSie kÃ¶nnen die durch die CovApp auf ihrem EndgerÃ¤t gespeicherten Daten jederzeit lÃ¶schen, indem Sie am Ende des Fragebogens âAntworten lÃ¶schenâ anklicken. \n\n<strong>3. Zu welchen Zwecken und auf welcher Rechtsgrundlage werden die Daten verarbeitet?</strong>\n\nDie CovApp soll es den Nutzerinnen und Nutzern erleichtern, selbst einzuschÃ¤tzen, ob ein weiterer AbklÃ¤rungsbedarf hinsichtlich einer COVID-19-Infektion besteht und bietet konkrete Handlungsempfehlungen. Die App dient damit der BekÃ¤mpfung der aktuellen COVID-19-Pandemie. Rechtsgrundlage der Verarbeitung sind Artikel 6 Abs. 1 Satz 1 lit. d) und e), Art. 9 Abs. 2 lit c), h), i) Datenschutz-Grundverordnung in Verbindung mit Â§ 14 Abs. 1 Nr. 3 Berliner Datenschutzgesetz.\n\n<strong>Im Ã¼brigen gilt die DatenschutzerklÃ¤rung der CharitÃ©, abrufbar unter:</strong>\n<a href="https://www.charite.de/service/datenschutz/" target="_blank">https://www.charite.de/service/datenschutz/</a>',
  data_privacy_headline: "DatenschutzerklÃ¤rung",
  disclaimer_headline: "Hinweis",
  disclaimer_paragraph_1: "Liebe Benutzerinnen und Benutzer,\ndie Coronavirus-Pandemie Ã¤ndert sich und wir werden in Zukunft in Deutschland vorrangig nur noch Personen testen, die folgende Kriterien erfÃ¼llen:\n<ul><li>Personen mit Kontakt zu Infizierten und mit Symptomen</li><li>Personen mit Risiken und Vorerkrankungen, die auf eine schwere COVID-19-Erkrankung hindeuten kÃ¶nnten</li><li>Personen, die das Coronavirus verbreiten kÃ¶nnten</li></ul>\nFalls Sie die App bereits genutzt haben, werden Sie merken, dass sich die Bewertung geÃ¤ndert hat. Dies geschieht auch weiterhin nach Angaben der relevanten BehÃ¶rden und Institutionen. Hierzu arbeiten wir eng mit dem Robert Koch-Institut und dem Bundesgesundheitsministerium zusammen.\n\nDie wichtigste Botschaft fÃ¼r Sie ist: Bleiben Sie zu Hause und helfen Sie so, die Ausbreitung zu verlangsamen.\n\n<strong>Rechtlicher Hinweis</strong>\nDie Nutzung dieser App ersetzt keine Ã¤rztliche Behandlung und erbringt keine diagnostische Leistung. Die CovApp dient der Verbesserung der HandlungsablÃ¤ufe in der Untersuchungsstelle der CharitÃ© und anderen Ambulanzen im Zusammenhang mit dem neuartigen Coronavirus SARS-CoV-2. Wenn Sie sich aktuell schwer krank fÃ¼hlen, rufen Sie umgehend einen Arzt/Ãrztin an.",
