  disclaimer_sub_headline_1: "Hinweis auf die Verwendung von Cookies",
  faq_content: '<strong>Ãber das Coronavirus</strong>\nDie wissenschaftliche Bezeichnung des neuartigen Coronavirus lautet âSARS-CoV-2â. Dieses Virus kann eine Atemwegserkrankung auslÃ¶sen. Diese Erkrankung trÃ¤gt den Namen âCOVID-19â. Weil sich die Infektionskrankheit COVID-19 bereits Ã¼ber LÃ¤nder und Kontinente hinweg ausgebreitet hat, spricht man von einer Pandemie.\n \n<strong>Die Ãbertragung</strong>\nDas neuartige Coronavirus wird Ã¼ber die sogenannte TrÃ¶pfcheninfektion Ã¼bertragen. Dies kann direkt von Mensch zu Mensch geschehen, wenn virushaltige TrÃ¶pfchen Ã¼ber die Mund- oder Nasenschleimhaut oder die Augenbindehaut in die Atemwege gelangen. Aber auch eine indirekte Ansteckung ist mÃ¶glich, wenn TrÃ¶pfchen Ã¼ber die HÃ¤nde mit den SchleimhÃ¤uten in Kontakt kommen. Krankheitszeichen treten im Durchschnitt nach 5 bis 6, maximal 14 Tagen nach einer Ansteckung auf.\n \n<strong>Die Krankheitszeichen</strong>\nWenn es zu einer Infektion mit dem Coronavirus gekommen ist, verlÃ¤uft diese fÃ¼r die die meisten Menschen leicht und ist nicht lebensbedrohlich. Das gilt auch fÃ¼r Schwangere und Kinder. Leichte Krankheitszeichen kÃ¶nnen grippeÃ¤hnliche Symptome wie trockener Husten, Abgeschlagenheit und Fieber sein. DarÃ¼ber hinaus leiden einige Patientinnen und Patienten unter Halskratzen, Kopf- und Gliederschmerzen und SchÃ¼ttelfrost. Derzeit geht man davon aus, dass 4 von 5 Infektionen mild verlaufen. Bei einigen Betroffenen kann das Virus jedoch zu einem schwereren Verlauf mit Atemproblemen bis hin zur beidseitigen LungenentzÃ¼ndung fÃ¼hren. Hier handelt es sich oftmals um Ã¤ltere Menschen und Patientinnen und Patienten mit Vorerkrankungen wie Herz-Kreislauf-Erkrankungen, chronischen Atemwegserkrankungen, Diabetes, Bluthochdruck oder Krebs. Diese schweren FÃ¤lle mÃ¼ssen intensivmedizinisch im Krankenhaus behandelt werden.\n\n<strong>SolidaritÃ¤t</strong>\nObwohl Deutschland Ã¼ber ein sehr gutes Gesundheitssystem verfÃ¼gt, ist dieses nicht auf einen steilen Anstieg insbesondere der intensiv medizinisch zu betreuenden Patientinnen und Patienten eingestellt. Gelingt es aber, den Anstieg schwerer Erkrankungen Ã¼ber einen lÃ¤ngeren Zeitraum hinauszuzÃ¶gern, werden Infizierte auch weiterhin gut versorgt werden kÃ¶nnen. Darum liegt das Hauptaugenmerk derzeit auf einer Entschleunigung der Infektionsrate. Dies kann nur gelingen, wenn alle BÃ¼rgerinnen und BÃ¼rger auf rÃ¤umliche Distanz gehen. Zum Schutz, insbesondere der Ã¤lteren Generation und chronisch Kranker, sollten alle darum die Situation ernst nehmen und soziale Kontakte so weit wie mÃ¶glich einschrÃ¤nken. Versorgen Sie aber bitte Ã¤ltere, hochbetagte oder chronisch kranke AngehÃ¶rige oder Nachbarn und alleinstehende, hilfsbedÃ¼rftige Menschen mit Lebensmitteln und Dingen des tÃ¤glichen Bedarfs. Haben Sie ein offenes Ohr fÃ¼r ihre Sorgen und kommunizieren Sie miteinander â aber halten Sie dabei bitte mindestens 1,5 Meter Abstand. Handeln Sie solidarisch.\n\n<strong>Schutz</strong>\nUm die Verbreitung des Coronavirus zu entschleunigen, vermeiden Sie unbedingt BerÃ¼hrungen beispielsweise bei der BegrÃ¼Ãung und waschen Sie Ihre HÃ¤nde regelmÃ¤Ãig und grÃ¼ndlich mindestens 20 Sekunden lang mit Wasser und Seife. Halten Sie beim Husten oder Niesen grÃ¶ÃtmÃ¶glichen Abstand â drehen Sie sich einfach von ihrem GegenÃ¼ber weg. Niesen Sie in die Armbeuge oder in ein Papiertaschentuch, das Sie danach immer entsorgen. Vermeiden Sie es, mit ihren HÃ¤nden Mund, Augen oder Nase zu berÃ¼hren. Teilen Sie GegenstÃ¤nde, die Sie mit Ihrem Mund berÃ¼hren, wie z.B. Tassen oder Flaschen, nicht mit anderen Personen. Bleiben Sie daheim.\n \n<strong>Therapie</strong>\nDie Behandlung der Infektion richtet sich nach der Schwere des Krankheitsbildes. Milde FÃ¤lle mÃ¼ssen nicht behandelt und kÃ¶nnen daheim auskuriert werden. Schwerere LungenentzÃ¼ndungen werden z.B. durch die Gabe von Sauerstoff, den Ausgleich des FlÃ¼ssigkeitshaushalts oder Antibiotikagabe zur Behandlung von bakteriellen Begleitinfektionen behandelt. Dies geschieht meistens im Krankenhaus.\n \n<strong>Impfung</strong>\nEs wird an der Entwicklung eines Impfstoffes gearbeitet. Momentan steht jedoch noch kein Impfstoff zur VerfÃ¼gung, der vor dem neuartigen Coronavirus schÃ¼tzt.\n\n<strong>Suchen Sie sich Hilfe</strong>\n Hilfetelefon "Gewalt gegen Frauen": <a href="tel:+498000116116">0800 116 116</a>\n Telefonseelsorge: <a href="tel:+498001110111">0800 111 0 111</a> oder <a href="tel:+498001110222">0800 111 0 222</a>\n Elterntelefon: <a href="tel:+498001110550">0800 111 0 550</a>\n "Nummer gegen Kummer" fÃ¼r Kinder und Jugendliche: <a href="tel:+49116111">116 111</a>\n \n<strong>Bleiben Sie informiert</strong>\nDas <a href="https://www.bundesgesundheitsministerium.de/coronavirus.html" target="_blank">Bundesministerium fÃ¼r Gesundheit</a> aktualisiert regelmÃ¤Ãig die Information zum Coronavirus.\n\nDer CharitÃ©-Wissenschaftler Prof. Dr. Christian Drosten hÃ¤lt sie im <a href="https://www.ndr.de/nachrichten/info/Corona-Podcast-Alle-Folgen-in-der-Uebersicht,podcastcoronavirus134.html" tarrget="_blank">NDR-Podcast</a> auf dem Laufenden.',
