  risk_spreading_caring_for_relatives: "<strong>Sie geben an, einen AngehÃ¶rigen zu pflegen. </strong>\nBitte klÃ¤ren Sie das weitere Vorgehen mit Ihrem zustÃ¤ndigen Hausarzt telefonisch ab und halten Sie Abstand zu Ihrem zu pflegenden AngehÃ¶rigen.",
  risk_spreading_living_alone: '<strong>Sie geben an alleine zu wohnen.</strong>\nWir raten Ihnen, tÃ¤glich mit einer vertrauten Person zu telefonieren, mit der Sie eine eventuelle Verschlechterung der Symptome besprechen kÃ¶nnen.\n\nBitte erkundigen Sie sich bei Nachbarn, FamilienangehÃ¶rigen oder Freunden, ob sie Ihnen helfen kÃ¶nnen. Falls dies nicht mÃ¶glich ist, kÃ¶nnen Sie sich an ehrenamtlich Helfende in ihrem Umkreis wenden, oder im Internet recherchieren (z.B unter dem Suchbegriff "Corona Nachbarschaftshilfe"). ',
