  risk_very_ill_below_sixty_five: '<strong>Sie geben an, unter Atemnot zu leiden.</strong>\nBitte melden Sie sich telefonisch bei Ihrem Hausarzt oder dem Ãrztlichen Bereitschaftsdienst <a href="tel:+49116117">(116117)</a>. Bei stÃ¤rker werden der Atemnot holen Sie sich bitte Ã¤rztliche Hilfe.',
  start_headline: "Handlungsempfehlungen und Informationen zum Coronavirus",
  start_legal_paragraph: "<strong>Bei der digitalen Anwendung handelt es sich nicht um eine Anwendung, die diagnostische Leistungen erbringt. Die Anwendung dient der Vereinfachung der HandlungsablÃ¤ufe in der Versorgung.</strong>",
