  start_paragraph_1_option_4: "Optimierung der PatientenstrÃ¶me",
  start_paragraph_2: '<p>Die CovApp ist eine von der CharitÃ© in Zusammenarbeit mit Data4Life entwickelte Software, mit der Sie innerhalb weniger Minuten einen Fragenkatalog beantworten und daraus spezifische Handlungsempfehlungen erhalten. DafÃ¼r werden Ihnen Fragen nach aktuellen Symptomen und mÃ¶glichen Kontakten gestellt. Diese App erbringt keine diagnostischen Leistungen. Sie erhalten aber nach Beantwortung des Fragenkatalogs konkrete Handlungsempfehlungen, Ansprechpartner und Kontakte sowie eine Zusammenfassung Ihrer Daten. Ziel ist es, Sie sicher durch die verschiedenen Angebote der Gesundheitsversorgung zu lotsen und Ihnen ggf. den Weg in KrankenhÃ¤user oder zu Untersuchungsstellen aufzuzeigen.</p><p>So kann Ihnen die CovApp helfen, die Notwendigkeit eines Arztbesuches oder Coronavirus-Tests besser einzuschÃ¤tzen. Die medizinisch relevanten Informationen fÃ¼r ein etwaiges ArztgesprÃ¤ch werden am Ende fÃ¼r Sie zusammengefasst.</p><p><h3 class="o-headline-3">Wie nutze ich die CovApp?</h3></p>\n<p><strong>1. Starten Sie die Befragung durch Klicken auf âFragebogen startenâ</strong></p>\n<p>Der Fragebogen nimmt ca. 5 Minuten Ihrer Zeit in Anspruch und stellt Ihnen Fragen zu aktuellen Symptomen, Vorerkrankungen und dem Kontakt zu VerdachtsfÃ¤llen. Versuchen Sie, die Fragen so genau wie mÃ¶glich zu beantworten.</p>\n<p><strong>2. Fragenkatalog und Empfehlungen</strong></p>\n<p>Zum Abschluss der Befragung erhalten Sie eine Ãbersicht Ã¼ber Ihre Antworten und spezifische Handlungsempfehlungen wie z.B. <ul><li>Anruf bei einer der Hotlines des Ã¶ffentlichen Gesundheitsdienstes</li>\n<li>Vorstellung beim Hausarzt</li>\n<li>HÃ¤usliche Isolierung</li>\n</ul></p><p><strong>3. Vorbereitung eines ArztgesprÃ¤chs</strong></p>\n<p>Falls ein ArztgesprÃ¤ch notwendig sein sollte, bieten einige Untersuchungsstellen die MÃ¶glichkeit, Ihre Antworten Ã¼ber einen QR-Code einzuscannen (u.a. an der CharitÃ©). Der Einsatz eines QR-Codes zur Ãbermittlung relevanter medizinischer Informationen unterstÃ¼tzt die weiterfÃ¼hrende Versorgung und hilft, die hohe Anzahl an Hilfe- bzw. Ratsuchenden schneller zu bearbeiten. \nFÃ¼r einen Besuch bei Ihrem Hausarzt oder in einer anderen Untersuchungsstelle auÃerhalb der CharitÃ© drucken Sie die Zusammenfassung am Ende aus oder zeigen Sie diese bei Ihrem Arztbesuch vor. </p>',
